<template>
   <div>
      {{dir}}
      <label>Ширина: 
         <input 
         type="number" 
         :value="width"
         @change="
            set('objWidth', 'width', $event.target.value + 'px'), 
            $emit('change', objWidth)
         "
      >
      </label><span>px</span>
      <br>
      <label>Тип линии: 
         <select 
            :value="value[1]"
            @change="
               set('objStyle', 'style', $event.target.value), 
               $emit('change', objStyle)
            "
         >
            <option value="solid">Cплошная</option>
            <option value="dashed">Пунктир</option>
            <option value="dotted">Точки</option>
            <option value="double">Двойная</option>
         </select>
      </label>
      <br>
      <label>
         Цвет: 
         <input 
            type="color"
            :value="value[2]"
            @change="
               set('objColor', 'color', $event.target.value), 
               $emit('change', objColor)
            "
         >
      </label>
      <hr>
   </div>
</template>
<script>
export default {
   props: ['value', 'dir'],
   data:()=>({
      objWidth: {},
      objStyle: {},
      objColor: {},
   }),
   beforeMount(){
      this.objWidth[this.dir+'width'] = '';
      this.objStyle[this.dir+'style'] = '';
      this.objColor[this.dir+'color'] = '';
   },
   computed: {
         width(){
            let result;
            if (this.value[0] !== undefined)
               [result] = this.value;
            else 
               result = '0';
            return parseFloat(result.replace(/[^.0-9]/gim,''));
         },
      },
      methods: {
         set(o, k, v){
            this[o][this.dir+k]=v;
         }
      }
   
}
</script>