<template>
   <form>
      <hr>
      <label>
         Шрифт: 
      <select 
         :value="value.family"
         @change="$emit('change', {family: $event.target.value})"
      >
         <option value="inherit">Наследуется</option>
         <option value="Arial">Arial</option>
         <option value="New Roman">New Roman</option>
      </select>
      </label>
      <br>
      <label>
         Курсив: 
      <select 
         :value="value.style"
         @change="$emit('change', {style: $event.target.value})"
      >
         <option value="inherit">Наследуется</option>
         <option value="normal">нет</option>
         <option value="italic">да</option>
      </select>
      </label>
      <br>
      <label>
         Жирный: 
      <select 
         :value="value.weight"
         @change="$emit('change', {weight: $event.target.value})"
      >
         <option value="inherit">Наследуется</option>
         <option value="normal">нет</option>
         <option value="bold">да</option>
      </select>
      </label>
      <br>
      <label>
         Размер:
         <input 
            type="number"
            :value="size"
            @change="$emit('change', {size: $event.target.value + 'px'})"
            placeholder="Наследуется"
         >
      </label>
      <br>
      <label>
         Цвет: 
         <input 
            type="color" 
            :value="color" 
            @change="$emit('change', {color: $event.target.value})"
         >
      </label>
      <br>
      <label>
         Регистр: 
      <select 
         :value="value.register"
         @change="$emit('change', {register: $event.target.value})"
      >
         <option value="inherit">Наследуется</option>
         <option value="capitalize">С заглавной</option>
         <option value="uppercase">В верхнем регистре</option>
         <option value="lowercase">В нижнем регистре</option>
         <option value="none">Не задан</option>
      </select>
      </label>
      <br>
   </form>
</template>

<script>
export default {
   props: ['value'],
   data:()=>({
      font: {},
   }),
   computed:{
      size(){
         let size;
         if(this.value.size==='inherit'){
            size = '';
         } else 
            size = parseFloat(this.value.size.replace(/[^.0-9]/gim,''));
         return size
      },
      color(){
         let some;
         if(this.value.color==='inherit'){
            some = '';
         } else 
            some = this.value.color;
         return some;
      },
   }
}
</script>