<template>
   <div id="side-panel">
      <el-collapse v-model="activePanel" accordion>
         <el-collapse-item
            name="toolbar"
         >
            <template slot="title">
               <span>Панель компонентов</span>
            </template>
            <draggable
               :list="components"
               :options="{
                  sort: false,
                  group: {
                     name: 'list',
                     pull: 'clone',
                     put: false
                  }
               }"
               :clone="(original)=> JSON.parse(JSON.stringify(original))"
            >
               <div
                  v-for="(item, index) of components"
                  :key="index"
                  class="toolbar-item"
               >
                  {{item.name}}
               </div>
            </draggable>
         </el-collapse-item>
         <el-collapse-item
            name="toolbar2"
         >
            <template slot="title">
               <span>Панель компонентов</span>
            </template>
            <draggable
               :list="components"
               :options="{
                  sort: false,
                  group: {
                     name: 'list',
                     pull: 'clone',
                     put: false
                  }
               }"
               :clone="(original)=> JSON.parse(JSON.stringify(original))"
            >
               <el-card
                  class="box-card toolbar-item"
                  v-for="(item, index) of components"
                  :key="index"
                  shadow="hover"
               >
                  {{item.name}}
               </el-card>
            </draggable>
         </el-collapse-item>
      </el-collapse>
   </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters } from 'vuex'

export default {
   components: {
      draggable
   },
   data:()=>({
      activePanel: 'toolbar'
   }),
   computed: {
      ...mapGetters({
         components: 'getComponents',
         test: 'getRootValue'
      })
   }
}
</script>

<style lang="scss" scoped>

   #side-panel {
      min-width: 400px;
      max-width: 400px;
      background-color: #fff;
      box-sizing: border-box;
      border-left: 1px solid red;
   }

   .toolbar {
      background-color: #b3b3b3;
      &-item {
         margin: 5px;
      }
   }
</style>