<template>
   <el-form label-width="180px" label-position="left">
      <el-form-item label="Размер">
         <el-checkbox v-model="checked"></el-checkbox>
      </el-form-item>
      <el-form-item label="Минимальная высота" v-if="checked">
         <el-input-number v-model="minHeight" :min="0" :step="typeMinHeight"/>
         <el-select class="size" v-model="type['min-height']">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Максимальная высота" v-if="checked">
         <el-input-number v-model="maxHeight" :min="0" :step="typeMaxHeight"/>
         <el-select class="size" v-model="type['max-height']">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Минимальная ширина" v-if="checked">
         <el-input-number v-model="minWidth" :min="0" :step="typeMinWidth"/>
         <el-select class="size" v-model="type['min-width']">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
      <el-form-item label="Максимальная ширина" v-if="checked">
         <el-input-number v-model="maxWidth" :min="0" :step="typeMaxWidth"/>
         <el-select class="size" v-model="type['max-width']">
            <el-option label="px" value="px"/>
            <el-option label="cm" value="cm"/>
            <el-option label="%" value="%"/>
         </el-select>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
   props: ['value'],
   data:()=>({
      checked: false,
      type: {
         'min-height': 'px',
         'max-height': 'px',
         'min-width': 'px',
         'max-width': 'px',
      },
   }),
   computed: {
      typeMinHeight() {
         let step = 1
         let type = this.type['min-height']
         if(type === 'cm')
            step = 0.1;
         else if (type === '%')
            step = 0.1;
         return step;
      },
      typeMaxHeight() {
         let step = 1
         let type = this.type['max-height']
         if(type === 'cm')
            step = 0.1;
         else if (type === '%')
            step = 0.1;
         return step;
      },
      typeMinWidth() {
         let step = 1
         let type = this.type['min-width']
         if(type === 'cm')
            step = 0.1;
         else if (type === '%')
            step = 0.1;
         return step;
      },
      typeMaxWidth() {
         let step = 1
         let type = this.type['max-width']
         if(type === 'cm')
            step = 0.1;
         else if (type === '%')
            step = 0.1;
         return step;
      },
      minHeight: {
         get () {
            return parseFloat(this.value['min-height'].replace(/[^.0-9]/gim,''));
         },
         set (v) {
            this.$emit('change', {'min-height': v + this.type['min-height']});
         }
      },
      maxHeight: {
         get () {
             return parseFloat(this.value['max-height'].replace(/[^.0-9]/gim,''));
         },
         set (v) {
            this.$emit('change', {'max-height': v + this.type['max-height']});
         }
      },
      minWidth: {
         get () {
            return parseFloat(this.value['min-width'].replace(/[^.0-9]/gim,''));
         },
         set (v) {
            this.$emit('change', {'min-width': v + this.type['min-width']});
         }
      },
      maxWidth: {
         get () {
            return parseFloat(this.value['max-width'].replace(/[^.0-9]/gim,''));
         },
         set (v) {
            this.$emit('change', {'max-width': v + this.type['max-width']});
         }
      }
   }
}
</script>

<style lang="scss" scoped>
   .size {
      width: 80px;
   }
</style>
