<template>
   <div id="tooltip">
      <button 
         class="header_btn" 
         @click.prevent="emit('edit')"
      >
         Edit
      </button>
      <button 
         class="header_btn" 
         @click.prevent="emit('del')"
      >
         Del
      </button>
   </div>
</template>

<script>
export default {
   methods: {
      emit(n){
         this.$emit('push', {
            pos: [],
            name: n,
            val: ''
         })
      }
   }
}
</script>

<style scoped>
   #tooltip {
      position: absolute;
      top: -20px;
      left: 0;
      width: 100%;
      z-index: 100;
      height: 20px;
      background-color: red;
   }
</style>