<template>
   <form>
      <hr>
      <label>
         Маржин сверху: 
         <input 
            type="number" 
            :step="stepTop"
            :value='top'
            min="0"
            @change="$emit('change', {top:  $event.target.value + type.top})" 
         >
         <select v-model="type.top">
            <option value="px">px</option>
            <option value="%">%</option>
            <option value="cm">cm</option>
         </select>
      </label>
      <br>
      <label>
         Маржин слева: 
         <input 
            type="number" 
            :step="stepLeft"
            :value='left'
            min="0"
            @change="$emit('change', {left:  $event.target.value + type.left})" 
         >
         <select v-model="type.left">
            <option value="px">px</option>
            <option value="%">%</option>
            <option value="cm">cm</option>
         </select>
      </label>
      <br>
      <label>
         Маржин справа: 
         <input 
            type="number" 
            :step="stepRight"
            :value='right'
            min="0"
            @change="$emit('change', {right:  $event.target.value + type.right})" 
         >
         <select v-model="type.right">
            <option value="px">px</option>
            <option value="%">%</option>
            <option value="cm">cm</option>
         </select>
      </label>
      <br>
      <label>
         Маржин снизу: 
         <input 
            type="number" 
            :step="stepBottom"
            :value='bottom'
            min="0"
            @change="$emit('change', {bottom:  $event.target.value + type.bottom})"
         >
         <select v-model="type.bottom">
            <option value="px">px</option>
            <option value="%">%</option>
            <option value="cm">cm</option>
         </select>
      </label>
   </form>
</template>

<script>
   export default {
      props: ['value'],
      data: ()=>({
         type: {
            top: 'px',
            left: 'px',
            right: 'px',
            bottom: 'px',
         },
      }),
      computed: {
         stepTop(){
            let step = ''
            if(this.type.top === 'cm'){ 
               step = '0.1';
            } else if (this.type.top === 'px' || '%')
               step = '1';
            return step;
         },
         stepLeft(){
            let step = ''
            if(this.type.left === 'cm'){ 
               step = '0.1';
            } else if (this.type.left === 'px' || '%')
               step = '1';
            return step;
         },
         stepRight(){
            let step = ''
            if(this.type.right === 'cm'){ 
               step = '0.1';
            } else if (this.type.right === 'px' || '%')
               step = '1';
            return step;
         },
         stepBottom(){
            let step = ''
            if(this.type.bottom === 'cm'){ 
               step = '0.1';
            } else if (this.type.bottom === 'px' || '%')
               step = '1';
            return step;
         },
         top(){
            return parseFloat(this.value.top.replace(/[^.0-9]/gim,''));
         },
         left(){
            return parseFloat(this.value.left.replace(/[^.0-9]/gim,''));
         },
         right(){
            return parseFloat(this.value.right.replace(/[^.0-9]/gim,''));
         },
         bottom(){
            return parseFloat(this.value.bottom.replace(/[^.0-9]/gim,''));
         }
      },
   }
</script>