<template>
   <form>
      <hr>
      <label>Overflow: 
         <select
            :value="value"
            @change="$emit('change', $event.target.value)"
         >
            <option value="inherit">Наследуется</option>
            <option value="auto">Автоматически</option>
            <option value="visible">Видно</option>
            <option value="hidden">Скрыть</option>
            <option value="scroll">Скрол</option>
         </select>
      </label>
   </form>
</template>

<script>
   export default {
      props:['value']
   }
</script>