<template>
   <form>
      <hr>
      <label>Варианты:</label>
      <br>
      <input 
         v-for="(option, index) of value"
         :key="index"
         type="text" 
         :value="value[index]"
         @change="$emit('changeOpt', {
            val: $event.target.value,
            index: index
         }
         )"
      >
      <br>
      <input 
         type="button" 
         value="Добавить"
         @click.prevent="$emit('addOpt')"
      >
      <br>
   </form>
</template>

<script>
   export default {
      props:['value']
   }
</script>
