<template>
   <el-form ref="form" label-width="180px" label-position="left">
      <el-form-item label="Позиционирование">
         <el-checkbox v-model="checked"></el-checkbox>
      </el-form-item>
      <el-form-item label="Направление:" v-if="checked">
         <el-select v-model="direction">
            <el-option label="столбцы" value="column"></el-option>
            <el-option label="ряды" value="row"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="Перенос строки:" v-if="checked">
         <el-select v-model="wrap">
            <el-option label="переносить" value="wrap"></el-option>
            <el-option label="не переносить" value="nowrap"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="По главной оси:" v-if="checked">
         <el-select v-model="justifyContent">
            <el-option label="с начала" value="flex-start"></el-option>
            <el-option label="с конца" value="flex-end"></el-option>
            <el-option label="по центру" value="center"></el-option>
            <el-option label="разрыв между" value="space-between"></el-option>
            <el-option label="разрыв вокруг" value="space-around"></el-option>
            <el-option label="разрыв равномерно" value="space-evenly"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="По доп. оси:" v-if="checked">
         <el-select v-model="alignItems">
            <el-option label="растягивать" value="stretch"></el-option>
            <el-option label="с начала" value="flex-start"></el-option>
            <el-option label="с конца" value="flex-end"></el-option>
            <el-option label="по центру" value="center"></el-option>
            <el-option label="по линии" value="baseline"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="Собств. полож.:" v-if="checked">
         <el-select v-model="alignSelf" placeholder="не задано">
            <el-option label="авто" value="auto"></el-option>
            <el-option label="нормальное" value="normal"></el-option>
            <el-option label="по центру" value="center"></el-option>
            <el-option label="с начала" value="flex-start"></el-option>
            <el-option label="с конца" value="flex-end"></el-option>
            <el-option label="растягивать" value="stretch"></el-option>
            <el-option label="по линии" value="baseline"></el-option>
         </el-select>
      </el-form-item>
      <el-form-item label="Сжатие:" v-if="checked">
         <el-input-number v-model="shrink" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="Растяжение:" v-if="checked">
         <el-input-number v-model="grow" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="Мин. размер:" v-if="checked">
         <el-input-number v-model="basis" :min="0" :placeholder="'auto'"></el-input-number>
      </el-form-item>
   </el-form>
</template>

<script>
   export default {
      props: ['value'],
      data: ()=>({
         checked: false
      }),
      computed: {
         direction: {
            get(){
               return this.value.direction
            },
            set(v) {
               this.$emit('change', {direction: v})
            }
         },
         wrap: {
            get(){
               return this.value.wrap
            },
            set(v) {
               this.$emit('change', {wrap: v})
            }
         },
         justifyContent: {
            get(){
               return this.value.justifyContent
            },
            set(v) {
               this.$emit('change', {justifyContent: v})
            }
         },
         alignItems: {
            get(){
               return this.value.alignItems
            },
            set(v) {
               this.$emit('change', {alignItems: v})
            }
         },
         alignSelf: {
            get(){
               return this.value.alignSelf
            },
            set(v) {
               this.$emit('change', {alignSelf: v})
            }
         },
         shrink: {
            get(){
               return this.value.shrink
            },
            set(v) {
               this.$emit('change', {shrink: v})
            }
         },
         grow: {
            get(){
               return this.value.grow
            },
            set(v) {
               this.$emit('change', {grow: v})
            }
         },
         basis: {
            get(){
               return this.value.basis
            },
            set(v) {
               this.$emit('change', {basis: v})
            }
         },
      }
   }
</script>