<template>
   <form>
      <hr>
      <label>
         <input type="checkbox" v-model="show.default">
         Граница : <span v-if="!show.default">нет</span>
      </label>
      <template v-if="show.default">
         <border-template 
            :dir="''"
            :value="[value.width, value.style, value.color]"
            @change="$emit('change', $event)"
         >
         </border-template> 
         <div v-for="(name, key) of names" :key="key">
            {{show[key]}}
            <label>
               <input type="checkbox" v-model="show[key]">
               {{name}} <span v-if="!show[key]">нет</span>
            </label>
            <border-template 
               v-if="show[key]"
               :dir="key + '-'"
               :value="[
                  value[key + '-width'],
                  value[key + '-style'],
                  value[key + '-color']
               ]"
               @change="$emit('change', $event)"
            >
            </border-template> 
         </div>
      </template>
   </form>
</template>

<script>
import BorderTemplate from "./BorderTemplate.vue";
   export default {
      components: {
         BorderTemplate
      },
      props: ['value'],
      data:()=>({
         names: {
            top: 'Верхняя граница: ',
            left: 'Левая граница: ',
            right: 'Правая граница: ',
            bottom: 'Нижния граница: ',
         },
         show: {
            default: false,
            top: false,
            left: false,
            right: false,
            bottom: false,
         },
      }),
      methods: {
         set(e){
            this.border[e.name] = e.obj;
         },
      }
   }
</script>