  getters: {
    //root
    getRootName (state) {
      return state.json.root.name
    },
    getRootStyle (state) {
      return state.json.root.options.styles
    },
    getRootValue (state) {
      return state.json.root.value
    },
    // toolbox
    getComponents (state) {
      return state.toolbox.components
    },
    // get Item
    getItem: state => p => {
      let obj = state.json.root.value
      let pos = p
      let last = pos.length - 1
      if (pos.length > 1) {
        for (let i = 0; i < last; i++) {
          obj = obj[pos[i]].childs
        }
      }
      return obj[pos[last]]
    }
  },
  mutations: {
    // add Item
    addItem (state, payload) {
      let obj = state.json.root.value
      let pos = payload.pos
      let val = payload.val
      if (pos.length > 0) {
        for (let i = 0; i < pos.length; i++) {
          obj = obj[pos[i]].childs
        }
      }
      obj.push(val)
    },
    // del Item
    delItem (state, payload) {
      let obj = state.json.root.value
      let pos = payload.pos
      let last = pos.length - 1
      if (pos.length > 0) {
        for (let i = 0; i < last; i++) {
          obj = obj[pos[i]].childs
        }
      }
      obj.splice(pos[last], 1)
    },
    // set styles/value
    setItemProps (state, payload) {
      let obj = state.json.root.value
      let pos = payload.pos
      let last = pos.length - 1
      let val = payload.val
      let type = payload.type
      let key = payload.key
      for (let i = 0; i < last; i++) {
        obj = obj[pos[i]].childs
      }
      if (typeof(val) === 'string') {
        obj[pos[last]].options[type][key] = val
      } else if (typeof(val) === 'object') {
        for (let i in val) {
          obj[pos[last]].options[type][key][i] = val[i]
        }
      }
    },
    //add option to select
    addOpt (state, payload) {
      let obj = state.json.root.value
      let pos = payload.pos
      let last = pos.length - 1
      let type = payload.type
      let key = payload.key
      for (let i = 0; i < last; i++) {
        obj = obj[pos[i]].childs
      }
      obj[pos[last]].options[type][key].push('')
    },
    //add option
    changeOpt (state, payload) {
      let obj = state.json.root.value
      let pos = payload.pos
      let last = pos.length - 1
      let type = payload.type
      let key = payload.key
      let index = payload.index
      let val = payload.val
      for (let i = 0; i < last; i++) {
        obj = obj[pos[i]].childs
      }
      obj[pos[last]].options[type][key].splice(index, 1, val)
      console.log(obj[pos[last]].options[type][key])
    }
  },
