<template>
   <div 
      class="block"
      :style="styles"
   >
      <label>{{value.options.values['my-label']}} </label>
      <select 
         :value="value.options.values.value"
         @change="$emit('change', {
            pos: [],
            val: $event.target.value,
            key: 'value',
            type: 'values'
         })"
      >
         <option 
            v-for="(item, index) of value.options.values['my-option']"
            :key="index"
         >{{item}}</option>
      </select>
   </div>
</template>

<script>
export default {
   props: {
      value: {
         type: Object,
         required: true
      }
   },
   computed: {
      styles () {
         let style = this.value.options.styles
         return {
            //bcg
            'background': style.background || 'inherit',
            //flex
            'display': 'flex',
            'flex-direction': style.flex.direction || 'column',
            'flex-wrap': style.flex.wrap || 'wrap',
            'justify-content': style.flex.justifyContent || 'flex-start',
            'align-items': style.flex.alignItems || 'stretch',
            'flex-shrink': style.flex.shrink || '1',
            'flex-grow': style.flex.grow || '1',
            'flex-basis': style.flex.basis || 'auto',
            'align-self': style.flex.alignSelf || '',
            //size
            'min-height': style.size['min-height'] || '',
            'max-height': style.size['max-height'] || '',
            'min-width': style.size['min-width'] || '',
            'max-width': style.size['max-width'] || '',
            //margin
            'margin-top': style.margin.top || '',
            'margin-left': style.margin.left || '',
            'margin-right': style.margin.right || '',
            'margin-bottom': style.margin.bottom || '',
            //padding
            'padding-top': style.padding.top || '',
            'padding-left': style.padding.left || '',
            'padding-right': style.padding.right || '',
            'padding-bottom': style.padding.bottom || '',
            //border
            'border-width': style.border.width || '',
            'border-style': style.border.style || '',
            'border-color': style.border.color || '',
            'border-top-width': style.border['top-width'] || '',
            'border-top-style': style.border['top-style'] || '',
            'border-top-color': style.border['top-color'] || '',
            'border-left-width': style.border['left-width'] || '',
            'border-left-style': style.border['left-style'] || '',
            'border-left-color': style.border['left-color'] || '',
            'border-right-width': style.border['right-width'] || '',
            'border-right-style': style.border['right-style'] || '',
            'border-right-color':style.border['right-color'] || '',
            'border-bottom-width': style.border['bottom-width'] || '',
            'border-bottom-style': style.border['bottom-style'] || '',
            'border-bottom-color': style.border['bottom-color'] || '',
            'border-radius': style.border.radius || '',
            'border-top-left-radius': style.border['top-left-radius'] || '',
            'border-top-right-radius': style.border['top-right-radius'] || '',
            'border-bottom-left-radius': style.border['bottom-left-radius'] || '',
            'border-bottom-right-radius': style.border['bottom-right-radius'] || '',
            //font
            'font-family': style.font.family || 'inherit',
            'font-style': style.font.style || 'inherit',
            'font-weight': style.font.weight || 'inherit',
            'font-size': style.font.size || 'inherit',
            'color': style.font.color || 'inherit',
            'text-transform': style.font.register || 'inherit',
            //overflow
            'overflow': style.overflow || 'auto',
            
         }
      },
   }
}
</script>